import React from 'react';
import { connect } from 'react-redux';

import Header from './../header';
import Nav from './../nav';
import Tbanner from './../Tbanner';
import Tinfo from './../Tinfo';
import Tprice from './../Tprice';
import Tuse from './../Tuse';
import Tcooperate from './../Tcooperate';
import Tcontact from './../Tcontact';
import ToTop from './../toTop';
import s from './Tob';

function Tob(data) {
    const num = data.data.tobNum.data.data[0] ? data.data.tobNum.data.data[0].num : null;
    return (
        <div className={s.box}>
            <Header data={data.data.forecast} />
            <Nav />
            <Tbanner num={num} />
            <Tinfo />
            <Tprice />
            <Tuse />
            <Tcooperate />
            <Tcontact />
            <ToTop />
        </div>
    );
}

export default connect(
    state => ({
        data: state,
    })
)(Tob);




